import * as React from 'react';
import { FA, LMR } from 'tonva';
import { NStockInfo } from './stockinfo';

export class GFunc {
  public static numberToFixString(n: number, w = 2) {
    return n === undefined ? '' : n.toFixed(w);
  }
  public static number(n: number, w=2) {
    return n === undefined ? '' : n.toFixed(w);
  }

  public static numberToMarketValue(n: number) {
    return n === undefined || isNaN(n) ? '' : Math.round(n/10000).toString(); // + '';
  }

  public static numberToString(n: number, precision = 2) {
    return n === undefined ? '' : n.toPrecision(precision);
  }

  public static numberToPrecision(n: number, precision = 4) {
    return n === undefined ? undefined : Number.parseFloat(n.toPrecision(precision));
  }

  public static percentToFixString(n: number) {
    return n === undefined || isNaN(n) ? '' : (n * 100).toFixed(2) + '%';
  }

  public static percent0(n: number) {
    return n === undefined || isNaN(n) ? '' : (n * 100).toFixed(0) + '%';
  }
  public static percent1(n: number) {
    return n === undefined || isNaN(n) ? '' : (n * 100).toFixed(1) + '%';
  }

  public static warningTypeString(t: string) {
    switch (t) {
      case 'gt': return '';
      case 'lt': return '';
      default: return t;
    }
  }

  public static predictCutRatio(r2:number) {
    return r2 * 2 - r2 * r2;
  }

  public static evaluatePricePrice(iRate:number, e1:number, e2:number, e3:number) {
    let r = 1 + iRate;
    let r2 = r * r;
    let r3 = r2 * r;
    return (e1/r + e2 /r2 + e3 / r3 + e3 / iRate / r3) / 2;
  }

  public static calculateV(z:number, divyield:number, pe:number) {
    if (pe === undefined || isNaN(pe)) {
      return undefined;
    }

    let r = 0;
    if (z !== undefined) {
      if (z > 0.25) {
        z = 0.25;
      }
      r += z * 100;
    }
    if (divyield !== undefined) {
      r += divyield * 100;
    }
    return r / pe;
  }

  public static calculateVN(z:number, e:number, bonus:number, price:number) {
    if (price === undefined || isNaN(price) || e === undefined || isNaN(e)) {
      return undefined;
    }

    let r = 0;
    if (z !== undefined && e > 0) {
      if (z > 0.25) {
        z = 0.24 + Math.pow((z-0.24)*100, 0.6667) / 100;
      }
      z = 1 + z;
      z = z * z * z - 1;
      r += z * e * 100;
    }
    if (bonus !== undefined && !isNaN(bonus)) {
      r += bonus * 100;
    }
    return r / price;
  }

  public static calculateZZ3(data:number[]) {
    let ret:number[] = [undefined, undefined, undefined, undefined] ;
    if (data === undefined)
      return ret;
    let len = data.length;
    if (len < 5)
      return ret;
    
    let funcz = (n:number, p:number) => {
      if (p <= 0 || n <= 0)
        return undefined;
      return ((n / p) - 1);
    }
    for (let i = 0; i < 4; ++i) {
      ret[i] = funcz(data[i+1], data[i]);
    }
    return ret;
  }

  public static caption = (value:string) => <span className="text-muted small">{value}</span>;

  public static MonthnoFromDay(day:number) {
    let year = Math.floor(day/10000);
    let month = Math.floor((day%10000)/100) - 1;
    return (year-1995)*12+month;
  }
  
  public static SeasonnoFromDay(day:number) {
    let year = Math.floor(day/10000);
    let month = Math.floor((day%10000)/100) - 1;
    let season = Math.floor(month / 3);
    return (year-1995)*4 + season;
  }
  
  public static SeasonnoFromYearMonth(year:number, month:number) {
    let season = Math.floor((month-1) / 3);
    return (year-1995)*4 + season;
  }

  public static SeasonnoToYearMonth(no: number) {
    let year = Math.floor(no / 4) + 1995;
    let month = (Math.floor(no % 4) + 1) * 3;
    return year * 100 + month;
  }
  
  public static SeasonnoToBeginEnd(season:number) {
    let year = (1995 + Math.floor(season / 4)) * 10000;
    let s = season % 4;
    if (s === 0) {
      return {begin: year + 101, end: year + 331};
    } 
    else if (s === 1) {
      return {begin: year + 401, end: year + 630};
    }
    else if (s === 2) {
      return {begin: year + 701, end: year + 930};
    }
    return {begin: year + 1001, end: year + 1231};
  }

  public static CalculatePredictAvg(arr: {predictpe:number}[]) {
    let ret : {avg20:number, avg50:number, avg100:number} = {avg20:undefined, avg50:undefined, avg100:undefined};

    let length = arr.length;
    let calcuOne = (count:number) : number => {
      let endIndex = count;
      if (endIndex > length)
        endIndex = length;
      if (endIndex >= 10) {
        let sum = 0;
        for (let i = 3; i < endIndex; ++i) {
          sum += arr[i].predictpe
        }
        return sum / (endIndex - 3);
      }
      else {
        return undefined;
      }
    }

    ret.avg20 = calcuOne(20);
    ret.avg50 = calcuOne(50);
    ret.avg100 = calcuOne(100);
    return ret;
  }

  public static CalculateValueAvg(arr: {v:number}[]) {
    let ret : {avg20:number, avg50:number, avg100:number, avg:number} = {avg20:undefined, avg50:undefined, avg100:undefined, avg:undefined};

    let length = arr.length;
    let calcuOne = (count:number) : number => {
      let endIndex = count;
      if (count === 0 || endIndex > length)
        endIndex = length;
      if (endIndex >= 10) {
        let sum = 0;
        for (let i = 3; i < endIndex; ++i) {
          sum += arr[i].v;
        }
        return sum / (endIndex - 3);
      }
      else {
        return undefined;
      }
    }

    ret.avg20 = calcuOne(20);
    ret.avg50 = calcuOne(50);
    ret.avg100 = calcuOne(100);
    ret.avg = calcuOne(0);
    return ret;
  }

  static renderSortCol(type:string, caption:string, sort: (sortType:string) => void) {
	  let onClick = () => {
		  sort(type);
	  }
	return <div className="c5 cursor-pointer text-center px-0" onClick={onClick}>
		{caption}
		<small><FA className="text-muted small ml-1" name="angle-down" /></small>
	</div>
  }
  static renderSortHeaders(sort: (sortType:string) => void) {
    return <div className="px-3 my-2">
      	<div className="px-5 c6 mr-3" />
		{GFunc.renderSortCol('tagv', '', sort)}
		{GFunc.renderSortCol('tagpe', 'TTM', sort)}
		{GFunc.renderSortCol('tagdp', '', sort)}
    </div>;
  }

  static renderStockInfoRow(row: NStockInfo, onClickName: (row:NStockInfo) => void, right:JSX.Element):JSX.Element {
    let { id, name, code, pe, roe, price, divyield, v, order, symbol, l, e, ep, e3, total } = row;
    let zzl = GFunc.calculateZZ3((row as any).dataArr);
    let left = <div className="ml-3 c6 cursor-pointer">
        <div className="small">{order} &nbsp; {code}</div>
      	<div className="text-primary" onClick={()=>onClickName(row)}>{name}</div>
	</div>;
	function renderValue(caption:string, value:number, valueType:'p0'|'p1'|'n1'|'n2'|'yi'):JSX.Element {
		const _cn = 'px-2 mb-2 text-right '; 
		let cn = _cn + 'c5';
		let cnYI = _cn + 'c5'
		let vStr:string;
		switch (valueType) {
			case 'p0': vStr = GFunc.percent0(value); break;
			case 'p1': vStr = GFunc.percent1(value); break;
			case 'n1': vStr = GFunc.number(value, 1); break;
			case 'n2': vStr = GFunc.number(value, 2); break;
			case 'yi': 
				vStr = GFunc.numberToMarketValue(value);
				cn = cnYI;
				break;
		}
		return <div key={caption} className={cn}>
			{GFunc.caption(caption)}<br />
			{vStr}
		</div>;
	  }
	  let rows:[string,number,'p0'|'p1'|'n1'|'n2'|'yi'][] = [
		['', v, 'n1'],
		['TTM', pe, 'n1'],
		['', divyield*100, 'n1'],
		['', price, 'n2'],
		['ROE', roe*100, 'n1'],
		['1', zzl[0], 'p0'],
		['2', zzl[1], 'p0'],
		['3', zzl[2], 'p0'],
		['4', zzl[3], 'p0'],
		['', l, 'p0'],
		['', total*price, 'yi'],
	];
	return <LMR className="px-1 py-1" left={left} right = {right} >
		<div className="d-flex flex-wrap" >
			{rows.map(v => renderValue(v[0], v[1], v[2]))}
		</div>
	</LMR>;
  }
}
